/* General reset and styling */
body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-image: url('assets/graveyard.gif');
  background-size: cover; /* Ensure the image covers the entire viewport */
  background-position: center; /* Center the image */
  background-repeat: none; /* Prevent repeating */
  background-color: #0D0D0D;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4); /* Adjust opacity for subtle effect */
  z-index: 0; /* Ensure it stays behind all content */
  pointer-events: none; /* Allow clicks to pass through */
}
.eater-regular {
  font-family: "Eater", serif;
  font-weight: 400;
  font-style: normal;
}

.pixelify-sans-title {
  font-family: "Pixelify Sans", serif;
  font-optical-sizing: auto;
  font-weight: 700; /* Bold weight */
  font-style: normal;
}

.gotham-body-text {
  font-family: "Gotham", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400; /* Regular weight */
  font-style: normal;
}

/* Container for the whole app */
#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  position: relative;
  z-index: 1; /* Place above the overlay */

}

/* Flexbox layout for content */
.app-container {
  display: flex;
  width: 100%; /* Take full width */
  height: 100vh; /* Full viewport height */
  justify-content: space-between;
  align-items: center;
  padding: 0;
  box-sizing: border-box;
}

/* Left 3D model area */
.canvas-container {
  flex: 2; /* Take 2/3 of the width */
  display: flex;
  flex-direction: column; /* Stack content vertically */
  justify-content: flex-end; /* Start at the top */
  align-items: center; /* Center the content horizontally */
  height: 100%; /* Full height */
  box-sizing: border-box;
  padding: 0px;
}

/* Skull canvas styles */
.skull {
  width: 100%; /* Take full width */
  height: 100%; /* Adjust to take most of the space */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  flex: 1;
}

/* Right menu */
.menu-container {
  flex: 1; /* Take 1/3 of the width */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly; /* Evenly spaced buttons */
  height: 100%; /* Match full height */
  padding: 20px; /* Add padding for spacing */
  box-sizing: border-box;
  margin-right: 100px;
}

/* Menu button styles */
/* Menu button styles */
.menu-button {
  padding: 15px 20px;
  font-size: 22px;
  color: #0E593E; /* White text */
  border: 3px solid #0E593E; /* Thicker white border */
  border-radius: 5px;
  background-color: #8C8C8C; /* Gray background for buttons */
  text-align: center;
  cursor: pointer;
  font-family: "Pixelify Sans", serif;
  font-optical-sizing: auto;
  font-weight: 700; /* Bold weight */
  font-style: normal;

  /* Add a drop shadow */
  box-shadow: 4px 4px 0px #0D0D0D;

  /* Transition for hover effects */
  transition: all 0.2s ease-in-out;
}

/* Hover effect: "pressed in" animation */
.menu-button:hover {
  background-color: #262626; /* Change background to white */
  color: #ffffff; /* Change text color to red */
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5), -1px -1px 3px rgba(255, 255, 255, 0.3); /* Reduced shadow for pressed effect */
  transform: translateY(2px); /* Pressed-down effect */
}

/* Active state: More pronounced "pressed in" effect */
.menu-button:active {
  animation: flash 0.4s ease-in-out; /* Trigger flash animation */
  transform: translateY(4px); /* Pressed-in effect */
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.7), inset -1px -1px 3px rgba(255, 255, 255, 0.4); /* Inner shadow */
  transform: translateY(4px); /* Further pressed-down effect */
}


/* Container for text or images below the skull */
.skull-info {
  text-align: center; /* Center align text and images */
  margin-top: 20px; /* Add some space above */
  width: 100%;
  margin-top: -360px;
  margin-left: -60px;
  margin-right: 60px;
  margin-bottom: 70px;
  color: #FFFFFF;
}

.skull-info h2 {
  font-size: 24px;
  color: #ffffff; /* Black or any color that fits */
  margin-bottom: 10px;
}

.skull-info p {
  font-size: 26px;
  color: #ffffff; /* Dark gray for description text */
  margin-bottom: -10px;
  font-family: "Pixelify Sans", serif;
  font-optical-sizing: auto;
  font-weight: 700; /* Bold weight */
  font-style: normal;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5), -1px -1px 3px rgba(255, 255, 255, 0.3); /* Reduced shadow for pressed effect */
}

.skull-info img {
  max-width: 80%; /* Make sure the image is responsive */
  height: auto; /* Maintain aspect ratio */
  border-radius: 8px; /* Optional: Rounded corners */
}

/* Social Media Section */
.social-media {
  display: flex;
  justify-content: center;
  gap: 25px; /* Space between icons */
  margin-top: -140px; /* Add spacing above the icons */
}

.followus p {
  color: #FFFFFF;
  font-family: "Pixelify Sans", serif;
  font-optical-sizing: auto;
  font-weight: 700; /* Bold weight */
  font-style: normal;
  margin-bottom: -100px;
  text-align: center;
}

/* Social Media Icons */
.social-icon {
  width: 40px; /* Adjust size */
  height: 40px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out; /* Add hover effect */
}

.social-icon:hover {
  transform: scale(1.2); /* Slight zoom-in on hover */
}

@keyframes flash {
  0% {
    background-color: #FFFFFF; /* Flash starts white */
    color: #0E593E; /* Red text during flash */
    box-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 20px rgba(255, 255, 255, 0.8);
  }
  50% {
    background-color: #0E593E; /* Flash briefly red */
    color: #FFFFFF; /* White text during mid-flash */
    box-shadow: 0 0 20px #0E593E, 0 0 30px #0E593E;
  }
  100% {
    background-color: #C0C0C0; /* Return to original background */
    color: #FFFFFF; /* Return to original text color */
    box-shadow: 4px 4px 0 #0E593E; /* Reset shadow */
  }
}

/* General mobile responsiveness */
@media (max-width: 768px) {
  /* Adjust app container for mobile layout */
  .app-container {
    flex-direction: column; /* Stack items vertically */
    justify-content: flex-start; /* Align items from the top */
    align-items: center; /* Center horizontally */
    padding: 10px; /* Add padding for spacing */
  }

  /* Skull canvas on mobile */
  .canvas-container {
    flex: 1; /* Take full width */
    width: 100%; /* Ensure it spans the screen */
    height: auto; /* Let height adjust naturally */
    align-items: flex-start; /* Align skull to the left */
  }

  .skull {
    width: 100%; /* Full width */
    height: 300px; /* Adjust height as needed */
    justify-content: flex-start; /* Push skull to the left */
    padding-left: 20px; /* Add padding to push skull away from the edge */
  }

  /* Buttons on mobile */
  .menu-container {
    flex: 1; /* Adjust the flex ratio */
    width: 100%; /* Full width */
    display: flex;
    flex-direction: column; /* Stack buttons vertically */
    align-items: center; /* Center the buttons */
    margin-top: 20px; /* Add space above the buttons */
  }

  .menu-button {
    width: 50%; /* Stretch buttons across most of the screen */
    margin-bottom: 10px; /* Space between buttons */
  }
  
  .followus p {
    color: #FFFFFF;
    font-family: "Pixelify Sans", serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Bold weight */
    font-style: normal;
    margin-bottom: 0px;
    text-align: center;
  }
  
  /* Social media icons on mobile */
  .social-media {
    margin-top: 20px; /* Add spacing above icons */
    justify-content: center; /* Center the icons */
  }

  .social-icon {
    width: 30px; /* Adjust icon size for smaller screens */
    height: 30px;
  }

  .app-container, .menu-container, .canvas-container {
    transition: all 0.3s ease-in-out;
  }
  .menu-button {
    font-size: 14px; /* Slightly smaller text */
    padding: 10px 15px; /* Adjust padding */
  }

  .skull-info p {
    font-size: 14px; /* Smaller text for details */
  }

  
}
